@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
html,body,#root{font-family:"Roboto", sans-serif;height:100%;margin:0;padding:0;width:100%;-moz-osx-font-smoothing:grayscale;-webkit-font-smoothing:antialiased}

.container-footer{height:100%;justify-content:center;align-items:center;display:flex}.container-footer .year-footer{text-align:right;padding-right:2rem}.container-footer .row{margin-right:0;margin-left:0;width:100%}.container-footer .links a{color:black}.container-footer .links a:not(:last-child){margin-right:1rem}

header .navbar-brand{padding:0;height:40px;width:40px}header .navbar-brand img{height:40px}header #responsive-navbar-nav a{text-align:center}@media (min-width: 767px){header .active-link{color:black !important;border-bottom:3px solid #000;margin-bottom:-3px}}@media (max-width: 767px){header .navbar-collapse{background:#f5f6fa}header .active-link{background-color:#e8f0fd}}

.container-notfound{width:100%}.container-notfound .code-notfound{margin-top:2rem;padding:0.3rem 0.5rem}.container-notfound .code-notfound span{color:#777777;margin-right:0.5rem}@media (max-width: 768px){.container-notfound .code-notfound{margin-top:0}}.container-notfound .message-notfound{font-size:1rem;margin-top:3rem;padding:0.3rem 0.5rem}

header{height:56px}main{min-height:calc(100% - 116px);padding:2rem 1rem}footer{height:60px}

